body {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.board {
    .board-container {
        display: inline-block;
        border-right: 1px solid #000;
        border-bottom: 1px solid #000;
        .board-row {
            display: flex;
            .board-item {
                border-top: 1px solid #000;
                border-left: 1px solid #000;
                width: 25px;
                height: 25px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }
}

.information-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.robot-scents {
    display: flex;
    flex-direction: column;
    b {
        margin-bottom: 10px;
    }
}

.board-params, .robot-instructions {
    display: flex;
    flex-direction: column;
}

.form-element {
    display: inline-grid;
    text-align: left;
    margin-bottom: 15px;
}